import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { toggleFilter, clearFilters, selectFilters } from '../results/resultListSlice';
import { includes, isEmpty } from 'lodash';

function Filters() {
    const filters = useSelector(selectFilters);
    const dispatch = useDispatch();

    const renderFilter = (num, filters) => {
        let className = `filter a${num}`;
        if (includes(filters, `a${num}`) || isEmpty(filters)) {
            className = `filter a${num} active`
        }

        return (
            <div className={className} onClick={() => dispatch(toggleFilter(`a${num}`))}>
                {`A${num}`}
            </div>
        );
    }

    return (
        <div>
            <h3 className="filters-title">Filters</h3>
            <div className="filters">
                {renderFilter(1, filters)}
                {renderFilter(2, filters)}
                {renderFilter(3, filters)}
                {renderFilter(4, filters)}
                {renderFilter(5, filters)}
                {renderFilter(6, filters)}
                {renderFilter(7, filters)}
                {renderFilter(8, filters)}
                {renderFilter(9, filters)}
                {renderFilter(10, filters)}
                {renderFilter(11, filters)}
                {renderFilter(12, filters)}
            </div>
        </div>
    );
}

export default Filters;