import { createSlice } from '@reduxjs/toolkit';
import { filter, includes, isEmpty } from 'lodash';

const applyFilters = (terms, filters, searchQuery) => {
  let filteredTerms;
  if (!isEmpty(filters) && !isEmpty(searchQuery)) {
    filteredTerms = filter(terms, elem => {
      let match = false;
      for (const filter of filters) {
        if (filter.includes('a')) {
          if (parseInt(filter.substr(1)) === elem.activity && elem.name.includes(searchQuery)) {
            match = true;
          }
        }
      }
      return match;
    });
  } else if (!isEmpty(filters)) {
    filteredTerms = filter(terms, elem => {
      let match = false;
      for (const filter of filters) {
        if (filter.includes('a')) {
          if (parseInt(filter.substr(1)) === elem.activity) {
            match = true;
          }
        }
      }
      return match;
    });
  } else if (!isEmpty(searchQuery)) {
    filteredTerms = filter(terms, elem => {
      return elem.name.includes(searchQuery);
    });
  } else {
    filteredTerms = terms;
  }
  return filteredTerms;
}

export const resultListSlice = createSlice({
  name: 'resultList',
  initialState: {
    terms: [],
    filters: [],
    filteredTerms: [],
    searchQuery: ''
  },
  reducers: {
    update: (state, action) => {
      state.terms = action.payload;
      state.filteredTerms = action.payload
    },
    toggleFilter: (state, action) => {
      if (includes(state.filters, action.payload)) {
        state.filters = filter(state.filters, elem => elem !== action.payload);
      } else {
        state.filters = [...state.filters, action.payload]
      }
      state.filteredTerms = applyFilters(state.terms, state.filters, state.searchQuery);
    },
    clearFilters: (state) => {
      state.filters = [];
      state.filteredTerms = state.terms;
    },
    updateSearchQuery: (state, action) => {
      state.searchQuery = action.payload;
      state.filteredTerms = applyFilters(state.terms, state.filters, state.searchQuery);
    },
    clearSearchQuery: state => {
      state.filters = [];
      state.searchQuery = '';
      state.filteredTerms = state.terms;
    },
  },
});

export const { update, toggleFilter, clearFilters, updateSearchQuery, clearSearchQuery } = resultListSlice.actions;

export const selectResultList = state => state.resultList.filteredTerms;
export const selectFilters = state => state.resultList.filters;
export const selectSearchQuery = state => state.resultList.searchQuery;

export default resultListSlice.reducer;