import React from 'react';

const handleSubmit = (e, props) => {
	e.preventDefault();
	const termsRef = props.firebase.db.collection("terms").add({
		activity: parseInt(e.target.activity.value),
		table: parseInt(e.target.table.value),
		name: e.target.name.value,
		definition: e.target.definition.value,
		...(e.target.parentStructure.value.length > 0 && {parentStructure: e.target.parentStructure.value})
	}); 
	e.target.name.value = "";
	e.target.definition.value = "";
}

function Admin(props) {
	return (
		<div>
			<h2>Add new term</h2>
			<form onSubmit={e => handleSubmit(e, props)}>
				<div>
					<label>Activity</label>
					<input name="activity" value="3"></input>
				</div>
				<div>
					<label>Table</label>
					<input name="table" value="1"></input>
				</div>
				<div>
					<label>Parent Structure</label>
					<input name="parentStructure"></input>
				</div>
				<div>
					<label>Name</label>
					<input name="name"></input>
				</div>
				<div>
					<label>Definition</label>
					<textarea name="definition"></textarea>
				</div>
				<button type="submit">Add new term</button>
			</form>
		</div>
	);
}

export default Admin;