import React from 'react';

function Result(props) {
  const data = props.result;
  const badgeClassName = `badge a${data.activity}`;

  return (
    <div className="result">
      <div className={badgeClassName}>
        <span>{`${data.activity}-${data.table}`}</span>
      </div>
      <div className="result-text">
        <h6 className="result-name">{data.name}</h6>
        {data.parentStructure && <b className="result-parent">part of the {data.parentStructure}</b>}
        <p className="result-definition">{data.definition}</p>
      </div>
    </div>
  )
}

export default Result;