import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { update, selectResultList } from './resultListSlice';
import { sortBy, isEmpty } from 'lodash';
import Result from './Result';

function ResultList(props) {
  const results = useSelector(selectResultList);
  const dispatch = useDispatch();

  if (isEmpty(results)) {
    const terms = props.firebase.db.collection('terms');
    terms.get().then(things => {
      const data = things.docs.map(elem => elem.data());
      dispatch(update(data));
    });
  }

  const renderResults = results => {
    const sortedResults = sortBy(results, ['activity', 'table', 'name']);
    return sortedResults.map(result => {
      const key = `${result.activity}-${result.table}-${result.name}`;
      return (
        <Result result={result} key={key}></Result>
      );
    });
  }


	return (
    <div className="results">
      {renderResults(results)}
    </div>
	);
}

export default ResultList;